  .promo__prev-button svg,
  .promo__next-button svg {
    stroke: #1e1744; }
  .promo__prev-button:hover,
  .promo__next-button:hover {
    border: solid 1px #3D5695; }
    .promo__prev-button:hover svg,
    .promo__next-button:hover svg {
