  align-items: center; }
  .promo__play-button svg {
    fill: #ffffff; }
  .promo__play-button:hover {
    border: solid 2px rgba(255, 255, 255, 0.8); }
    .promo__play-button:hover svg {
      fill: rgba(255, 255, 255, 0.8); }
